/* eslint-disable no-unused-vars */
import React, { Fragment,useEffect,useState } from 'react';
import axios from 'axios';
import { MDBDataTable } from 'mdbreact';
import {personal_details_init,personal_details_errors_init} from './loans-constants';
import {routes} from '../../constants';
import InlineMessage from '../Forms/InlineMessage';

const ActiveLoans = () => {
	const[loans, setLoans] = useState([]);
	const[inline,setInline] = useState({message:'',message_type:'info'});

	useEffect(() => {        
		const fetchAPI = async () => {
			await axios.get(routes.loans_api_endpoint).then(results => {
				if(results.status === 200){
					return results.data;
				}else{
					throw new Error('there was an error fetching loans-please check your internet connection');
				}
			}).then(loans => {
				setLoans(loans);
				setInline({message:'successfully loaded loans'});        
			}).catch(error => {
				setInline({message:'error : '+ error.message,message_type:'error'});
			});
		};

		fetchAPI();
		return () => {
            
		};
	}, []);

	const returnData = () => {

		const prepareLoans = () => {
			let preparedLoans = [];

			loans.forEach(loan => {        
				preparedLoans.push(Object.entries(loan));
			});
			return preparedLoans;
		};

		const data = {
			columns: [
				{
					label: 'Client ID',
					field: 'client_id',
					sort: 'asc',
					width: 150
				},
				{
					label: 'ID Number',
					field: 'id',
					sort: 'asc',
					width: 270
				},
				{
					label: 'Surname',
					field: 'surname',
					sort: 'asc',
					width: 200
				},
				{
					label: 'Full Names',
					field: 'names',
					sort: 'asc',
					width: 100
				},
				{
					label: 'ALLPS',
					field: 'allps',
					sort: 'asc',
					width: 150
				}
			],
			rows:  prepareLoans()
		};
    
		return data;

	};
  
	let data =  returnData();
  console.log('DATA',data);
	

	return (
		<Fragment>
			<div className="box box-body">
				<div className="box box-header">
					<h3 className="box-title">
						<strong>
							<i className="fa fa-archive"> </i> Active Loans
						</strong>
					</h3>
				</div>

				<div className="box box-footer">
					{ data ?
						<MDBDataTable striped bordered hover data={data} /> : ''
					}
				</div>
			</div>
			<div className="box-footer">
			</div>
		</Fragment>
	);
};

export default ActiveLoans;
